<template>
    <div class="check"
         :class="{active:values.indexOf(parseFloat(label)) != -1,disabled:disabled}"
        @click="selectCk"
    >
        <span class="check-icon"></span>
        <span class="check-txt"><slot></slot></span>
        <!--禁用时候覆盖 防止能点击-->
        <span class="over" @click.stop></span>
    </div>
</template>
<script>
    export default {
        name: 'radio',
        props:{
            label:{
                type:[String,Number],
                default:null
            },
            value:{
                type:Array,
                default:[]
            },
            disabled:{
                type:Boolean,
                default:false
            }
        },
        data() {
            return {
                values:[]
            }
        },
        watch:{
            value(d){
                //console.log('wc',d);
                this.doData();
            }
        },
        mounted(){
            //console.log(this.label);
            //console.log(this.value);
            //console.log(this.value.indexOf(this.label));
            this.doData();
        },
        methods:{
            //处理数据
            doData(){
                this.values = this.value;
                //把数组处理成Number
                let newValues = [];
                for(let i=0;i<this.values.length;i++){
                    newValues.push(parseFloat(this.values[i]));
                }
                this.values = newValues;
            },
            //选择点击
            selectCk(){
                console.log('this.values');
                console.log(this.values);
                console.log(this.values.indexOf(parseFloat(this.label)));
                if(this.values.indexOf(parseFloat(this.label)) === -1){
                    //console.log('dd');
                    //this.values.push(parseFloat(this.label));
                }
                this.values.push(parseFloat(this.label));
                console.log(this.values);
            }
        }
    }
</script>
<style scoped>
    .check{
        height: 30px;overflow: hidden;line-height: 30px;float:left;cursor:pointer;
    }
    .check-icon{width: 24px;height: 24px;float:left;display: block;
        background: url("../../assets/images/icon-check.png") no-repeat center;
        background-size: 100%;margin-top:3px;
    }
    .check-txt{display: block;float:left;font-size: 14px;color:#333;margin-left:5px;margin-right: 10px;}
    .check.active .check-icon{background: url("../../assets/images/icon-check-active.png") no-repeat center;
        background-size: 100%;}

    .check.disabled{opacity: 0.5;position: relative;}
    .over{position: absolute;top:0px;left:0px;right:0px;bottom:0px;display: none;}
    .radio.disabled .over{display: block;cursor: no-drop;}
</style>


