<template>
    <div class="wl-checkbox">
        <div class="h-headtitle">Checkbox 多选框</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                文字超链接
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">用于在选中和非选中状态之间进行切换。。</div>
            <div class="hb-a">
                <Checkbox v-model="check1" label="1" >复选框1</Checkbox>
                <Checkbox v-model="check1" label="2" >复选框2</Checkbox>
            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code >
                &lt;Checkbox v-model="check1" label="1" &gt;复选框1&lt;/Checkbox&gt;
                &lt;Checkbox v-model="check1" label="2" &gt;复选框2&lt;/Checkbox&gt;
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>

        </div>

    </div>
</template>

<script>
    import Checkbox from '@/components/template/Checkbox.vue'
    export default {
        name: 'wlcheckbox',
        data() {
            return {
                check1:['1']
            }
        },
        mounted(){
            this.doCode();
        },
        methods:{
            //一进来需要执行的
            doCode(){
                $('.view-code-btn').click(function (){
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
        },
        components:{
            Checkbox
        }
    }
</script>
<style scoped>

</style>



